<template>
  <div class="timeline__wrapper" :id="id">
    <div class="timeline__modal" :class="{'active': modal__active}"></div>
    <div class="timeline__date" :class="{'chosenDay': chosen}">
      <p class="date" :class="{'today': isToday}">{{getDateFormat}}</p>
    </div>
  <div class="container" :class="{'chosenDay': chosen}">
      <div class="container__line" :class="{'now':isToday}"></div>
   <div class="container__left">
      <div class="namesOfFields">
          <div class="field__empty"></div>
          <div v-for = "value in facility" :key = "value.id" class="field__name">
            <p class="field__name__value">{{value.id}}</p>
          </div>
      </div>
  </div>
  <div class="container__right">
    <div class="timeline">
       <div v-for = "time, index in timelines" :key="index" class="timecell">
         <p class="timecell__value">{{time}}</p>
       </div>
     </div>
     <div v-for = "value in facility" :key = "value.id" class = "timeline__orders">
        <div v-for = "time, index in timelines" :key="index" :id="index" class="timeline__order" @click="onMakeOrder($event, index, `${id}_${value.id}`)">
          <p :id="index">+</p>
        </div>
     </div>
 </div>
<OrderPopup v-if="popupVisible" @closePopup ='togglePopup(false)' :duration="this.duration" :durationRange="this.durationRange" :startTime="this.startTime" :endTime="this.endTime" :date="this.getDateFormat"></OrderPopup>
</div>
</div>
  
 
</template>
<script>
import OrderPopup from '@/components/OrderPopup'
export default {
  props: ['date', 'id'],
  components: {
    OrderPopup
  },
  data() {
    return {
      facility: {
        field1: {
          id: 1,
          name: "#1 Spartak Arena",
          startWork: 9,
          endWork: 23,
          price: 5000,
        },
        field2: {
          id: 2,
          name: "#2 Lokomotiv Arena",
          startWork: 9,
          endWork: 23,
          price: 5000
        },
        field3: {
          id: 3,
          name: "#3 Dinamo Arena",
          startWork: 9,
          endWork: 23,
          price: 5000,
        },
      },
      timelines: [],
      modal__active: false,
      chosen: false,
      isToday: false,
      clicked: false,
      rangeChosen: false,
      firstChosenTime: null,
      lastChosenTime: null ,
      popupVisible: false, 
      duration: 30,
      durationRange: 0,
      startTime: '',
      endTime: '',
      timenowIdx: 0 
    };
  },
  mounted() {
    /*Статичные данные, нормальные данные получу с сервера, пока данные не готовы*/ 
    for (let i = 6; i < 30; i++) {
      let real_i = i%24;
      const time = real_i < 10 ? `0${real_i}` : `${real_i}`;
      this.timelines.push(`${time}:00`);
      this.timelines.push(`${time}:30`);
    }
    const d = new Date();
    this.isToday = `${this.date.getDate()}/${this.date.getMonth()}/${this.date.getFullYear()}}`===`${d.getDate()}/${d.getMonth()}/${d.getFullYear()}}`;
    let hours = d.getHours();
    let minutes = d.getMinutes();
    let timenow =  `${hours<10?`0${hours}`:hours}:${minutes>=30?'30':'00'}`
    for(let i = 0;i<this.timelines.length;i++){
      if(this.timelines[i]===timenow){
          this.timenowIdx = i;
      }
    }
    let drawLine = document.getElementsByClassName('container__line')[0]

    drawLine.style.left = `${this.timenowIdx*50+83}px` //взял ширину ячейки(49px)+1px отступ для каждой ячейки+нужно чтобы по центру ячейки было(49/2)+начальный отступ 37+21=58px
    
  },  
  methods: {
    onMakeOrder(event, index, fieldIndex){
        //Хочу чтобы при клике на текст и на саму дивку срабартывал метод,так как у меня в <div> заложен тег <p> мне нужно достать дивку если был нажат <p>
    let element = event.target.tagName==="DIV"?event.target:event.target.parentElement;

    /*Нужно выбрать все время что находится между двумя. К примеру, если админ нажал 8:30, а потом 10:00, 
    то все ячейки времени с 8:30 до 10:00 должны выбраться*/

    /*Если оба конца выбраны и админ нажимает на другую ячейку , 
    то предыдущий выбор должен очиститься и должна выбраться последняя ячейка что была выбрана*/
    
    /* Нужно отслеживать индексы полей, дней чтобы админ не мог выбрать время в разных полях, днях*/
    /*Иными словами можно оформить только один заказ за один раз*/
    this.chosen = true;
    if(this.firstChosenTime===null){
        this.firstChosenTime = {element, index, fieldIndex}
        this.toggleClicked(this.firstChosenTime.element, null, 'ADD')
    }
    else if(this.lastChosenTime===null){
        if(this.firstChosenTime.fieldIndex!==fieldIndex){
            this.toggleClicked(this.firstChosenTime.element, null, 'REMOVE');
            this.clearChoice();
            this.firstChosenTime = {element, index, fieldIndex}
            this.toggleClicked(this.firstChosenTime.element, null, 'ADD')
        }
        else{
              if(this.firstChosenTime.index===index){
                this.toggleClicked(this.firstChosenTime.element, null, 'REMOVE');
                this.clearChoice();
                this.chosen = false;
            }
            else if(index<this.firstChosenTime.index){
              //Чтобы админ мог выбрать интервал backwards
              this.lastChosenTime = {...this.firstChosenTime}
              this.firstChosenTime = {element, index, fieldIndex}
              this.toggleClicked(this.firstChosenTime.element, this.lastChosenTime.element, 'ADD')
            }
            else{
              this.lastChosenTime = {element, index, fieldIndex}
              this.toggleClicked(this.firstChosenTime.element, this.lastChosenTime.element, 'ADD')
              }
          }
    }
    else if(this.lastChosenTime&&this.firstChosenTime){
        this.toggleClicked(this.firstChosenTime.element, this.lastChosenTime.element, 'REMOVE');
        this.clearChoice();
        this.firstChosenTime = {element, index, fieldIndex}
        this.toggleClicked(this.firstChosenTime.element, null,'ADD')
    }
        this.togglePopup(true)
    },



    togglePopup(bool){
      this.popupVisible = bool;
      let clickedElements = [...document.getElementsByClassName('clicked')]
      if(!bool){
        this.modal__active = false
        for(let i = 0;i<clickedElements.length;i++){
          clickedElements[i].classList.remove("clicked");
        }
        clickedElements = []
        this.chosen = false
      }
      else{
        let clickedOrder = clickedElements.map(e=>Number(e.firstChild.id))
        this.startTime = this.timelines[clickedOrder[0]]
        this.endTime = this.timelines[clickedOrder[clickedOrder.length-1]+1]
        this.durationRange = clickedOrder.length-1
        this.modal__active = true
      }
      
    },
    clearChoice(){
      this.firstChosenTime = null
      this.lastChosenTime = null
    },

    
    toggleClicked(firstElement, secondElement, method){
      if(firstElement&&secondElement){
         let elementBetween = firstElement
          while(elementBetween!==secondElement){
            if(method==='ADD'){
                elementBetween.classList.add('clicked')
            }
            else if(method==='REMOVE'){
                elementBetween.classList.remove('clicked')
            }
                elementBetween = elementBetween.nextSibling;
            }
            if(method==='ADD'){secondElement.classList.add("clicked")}
            if(method==='REMOVE'){secondElement.classList.remove("clicked")}
      }
      else if(firstElement){
        if(method==='ADD'){firstElement.classList.add("clicked")}
        else if(method==='REMOVE'){firstElement.classList.remove("clicked")}
      }
    } 
  },
  computed: {
    getDateFormat: function(){
      const today = this.date;
      const dd = String(today.getDate()).padStart(2, '0');
      const mm = String(today.getMonth());
      const dayOfWeek = today.getDay();
      let day; // Чтобы превратить 0-Воскресенье, 1-Понедельник
      let month; //0-Январь, 1- Февраль
      switch(mm){
        case '0': 
           month = 'Января';
           break;
        case '1': 
           month = 'Февраля';
           break;
        case '2': 
           month = 'Марта';
           break;
        case '3': 
           month = 'Апреля';
           break;
        case '4': 
           month = 'Мая';
           break;
        case '5': 
           month = 'Июня';
           break;
        case '6': 
           month = 'Июля';
           break;
        case '7': 
           month = 'Августа';
           break;
        case '8': 
           month = 'Сентября';
           break;
        case '9': 
           month = 'Октября';
           break;
        case '10': 
           month = 'Ноября';
           break;
        case '11': 
           month = 'Декабря';
           break;
      }

      switch(dayOfWeek){
        case 0:
          day = "Вс";
          break;
        case 1:
          day = "Пн";
          break;
        case 2:
          day = "Вт";
          break;
        case 3:
          day = "Ср";
          break;
        case 4:
          day = "Чт";
          break;
        case 5:
          day = "Пт";
          break;
        case  6:
          day = "Сб";
      }
      return `${dd} ${month}, ${day}`;
    }
  } 
};
</script>
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap');
<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  user-select: none;
}
.timeline__date{
  height: 44px;
  background: white;
  width: 100%;
}
.date{
  position: sticky;
  height: 24px;
  left: 8px;
  top: 20px;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  color: #000;
}
.today{
  color: #C03623;
}
.container, .timeline, .timeline__orders{
  display: flex;
}
.container{
  position: relative;
  width: 100vw;
}
.now{
  width: 1px;
  height: 100%;
  position: absolute;
  background-color: red;
  z-index: 5;
}
.translate{
  transform: translateX(200px);
}
.container__left{
  width: 37px;
}
.field__name, .field__empty{
  height: 30px;
}
.field__name>p{
  height: 20px;
  width: 23px;
  margin: 5px 7px;
}
.field__name__value, .timecell__value{
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0em;
  text-align: center;
}
.timecell{
  width: 49px;
  height: 30px;
  margin-right: 1px;
}
.timeline__order{
  margin-left: 1px;
  background: white;
}
.timecell__value{
  width: 35px;
  height: 20px;
  margin: 5px 8px 5px 6px;
}
.clicked{
  outline: 1px solid red;
}


.timeline__orders{
  padding-left: 21px;
  cursor: pointer;
}
.timeline__orders, .field__name{
  margin-bottom: 10px;
}
.timeline__order>p{
  width: 7px;
  height: 20px;
  margin: 5px 21px;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 20px;
  text-align: center;
  color: #000;
  opacity: 1;
}

.active{
  background-color: rgba(255,255,255, 0.8);
  position: fixed;
  top:0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 10;
}
.timeline__date.chosenDay{
  z-index: 100;
  background: #fff;
}
.container.chosenDay{
  z-index: 100;
  background: #192824;
}
</style>