<template>
  <div id="app">
    <div class="timeline__content">
      <!-- <InfiniteScroll :items="dates" @refetch = "fetch"> -->
      <template v-for="date, index in dates" :key="index">
        <Timeline :date="date.today" :id="date.id" :chosen="chosen"/>
      </template>
    <!-- </InfiniteScroll> -->
    </div>
  </div>
</template>

<script>
// import InfiniteScroll from "@/components/InfiniteScroll";
import Timeline from '@/components/Timeline'

export default {
  components: {
    // InfiniteScroll,
    Timeline
  },
  data() {
    return {
      dates: [],
      api__id: "60dfd6aeb4f02815aff81a48",
      BASE_URL: "https://dummyapi.io/data/api",
      firstChosenTime: null,
      lastChosenTime: null
    };
  },
  methods: {
    fetch() {
        let today = new Date();
        for(let i = 0;i<=30;i++){
          let tomorrow = new Date(today);
          this.dates.push({id:i, today})
          tomorrow.setDate(today.getDate()+1);
          today = tomorrow;

        }
    },
  },
  mounted() {
    this.fetch();
  },
};
</script>

